.home{display:flex;flex-direction:column;margin-top:30px}.home .apartments{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:30px;gap:30px;background-color:#f6f6f6;padding:40px 30px;border-radius:25px;margin-top:20px}
:export{primary-color:#ff6060;secondary-color:#f6f6f6;font-family:"Montserrat",sans-serif}*{font-family:"Montserrat",sans-serif}body{margin:20px 50px 0 50px}
.banner{position:relative;width:100%;height:223px;border-radius:25px;margin-top:20px}.banner img{display:inline-block;object-fit:cover;border-radius:inherit;width:inherit;height:inherit}.banner .overlay{position:absolute;object-fit:cover;width:100%;inset:0;border-radius:inherit;background:rgba(0,0,0,.3);display:flex;align-items:center;justify-content:center}.banner .overlay p{display:inline-block;color:#fff;font-weight:500;font-size:48px}
.card{position:relative;overflow:hidden;border-radius:10px}.card img{display:block;object-fit:contain;height:340px;border-radius:inherit}.card .overlay{position:absolute;object-fit:contain;border-radius:inherit;width:100%;height:100%;z-index:1;left:0;bottom:0;background:linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(255, 255, 255, 0.2) 50%);display:flex;align-items:flex-end;justify-content:flex-start}.card .overlay p{display:inline-block;margin-left:1em;color:#fff;inset:0;font-weight:500;font-size:18px}
:export{primary-color:#ff6060;secondary-color:#f6f6f6;font-family:"Montserrat",sans-serif}.error{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:600px}.error *{display:inline-block;color:#ff6060;margin:0}.error .code{font-size:288px;font-weight:700}.error .alert{font-size:36px;font-weight:500}.error a{font-size:18px;font-weight:500}
:export{primary-color:#ff6060;secondary-color:#f6f6f6;font-family:"Montserrat",sans-serif}.collapse{display:flex;flex-direction:column;position:relative;margin-top:30px}.collapse .layoutPurpose{position:absolute;z-index:-1;top:0;left:0;width:100%;height:100%;background-color:#f6f6f6;border-radius:10px}.collapse .title{display:flex;justify-content:space-between;align-items:center;height:52px;background-color:#ff6060;border-radius:10px;padding:10px 20px 10px 10px;cursor:pointer}.collapse .title .titleText{display:inline-block;color:#fff;margin:0;font-size:24px;font-weight:500}.collapse .title .arrowOpen{display:block;width:24px;-webkit-transform:rotate(0turn);transform:rotate(0turn);transition:-webkit-transform 300ms ease-in-out;transition:transform 300ms ease-in-out;transition:transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out}.collapse .title .arrowClose{-webkit-transform:rotate(0.5turn);transform:rotate(0.5turn)}.collapse .contentText{overflow:hidden;background-color:#f6f6f6;color:#ff6060;border-bottom-left-radius:10px;border-bottom-right-radius:10px;font-size:18px;font-weight:400;max-height:300px;transition:max-height 400ms ease-in-out}.collapse .contentText p{margin:0;padding:15px}.collapse .contentText .equipment{list-style-type:none}.collapse .hideContentText{max-height:0}
.infos{margin:50px 100px}
:export{primary-color:#ff6060;secondary-color:#f6f6f6;font-family:"Montserrat",sans-serif}.header{display:flex;justify-content:space-between;align-items:center;height:68px}.header img{display:inline-block}.header div a{font-family:"Montserrat";font-weight:500;font-size:24px;color:#ff6060;margin-left:57px}
.footer{display:flex;flex-direction:column;justify-content:center;align-items:center;height:209px;width:100%;margin-top:30px;background-color:#000}.footer img{display:inline-block;height:auto;width:122px}.footer p{font-family:"Montserrat";font-weight:500;font-size:24px;color:#fff;margin:10px 0 0 0}
